export default {
	name: "index-list",
	props: {
		item: Object,
		index: Number
	},
	data() {
		return {
			isguanzhu: this.item.isguanzhu,
			infonum: this.item.infonum
		}
	},
	onLoad() {
		console.log(this.listsData);
	},
	methods: {
		//点击关注事件
		guanzhu() {
			this.isguanzhu = true;
			uni.showToast({
				title: "关注成功",
			})
		},
		//顶踩事件
		caozuo(index, type) {
			switch (type) {
				case 'ding':
					if (this.infonum.index == 1) {
						return;
					};
					this.infonum.dingnum++;
					if (this.infonum.index == 2) {
						this.infonum.cainum--;
					}
					this.infonum.index = 1;
					break;
				case 'cai':
					if (this.infonum.index == 2) {
						return
					};
					this.infonum.cainum++;
					if (this.infonum.index == 1) {
						this.infonum.dingnum--;
					}
					this.infonum.index = 2;
					break;
				default:
					break;
			};
		},
		//进入详情页
		opendetail() {

		}
	}
}
